  // API   
  const handleDeleteKey = (serviceId) => {
    setApiKeys((prev) => ({
      ...prev,
      [serviceId]: "",
    }));
    localStorage.removeItem(`${serviceId}_api_key`);
  };

  const handleKeyChange = (serviceId, value) => {
    setApiKeys((prev) => ({
      ...prev,
      [serviceId]: value,
    }));
  };

