  const renderPage = () => {
    switch (currentPage) {
      case "home":
        return <VoiceTextSummarizer />;
      case "timer":
        return <PomodoroTimer onTimerComplete={handleTimerComplete} />;
      case "block":
        return <BlockPage onBlockComplete={handleBlockComplete} />;
      case "productivity":
        return <ProductivityManager />;
      case "apitest":
        return <APITest />;
      default:
        return <VoiceTextSummarizer />;
    }
  };

