// AI   

class AIServiceManager {
  constructor() {
    this.services = {
      claude: {
        name: "Claude",
        proxyUrl: "/api/ai/claude",
      },
      gpt: {
        name: "GPT",
        proxyUrl: "/api/ai/gpt",
      },
      groq: {
        name: "Groq",
        proxyUrl: "/api/ai/groq",
      },
      perplexity: {
        name: "Perplexity",
        proxyUrl: "/api/ai/perplexity",
      },
      gemini: {
        name: "Gemini",
        proxyUrl: "/api/ai/gemini",
      },
    };
  }

  async processWithAI(serviceId, text, userToken) {
    const service = this.services[serviceId];
    if (!service) {
      throw new Error(`  AI : ${serviceId}`);
    }

    const prompt = this.createPrompt(text);

    try {
      //    AI API 
      const response = await fetch(service.proxyUrl, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          prompt: prompt,
          apiKey: userToken,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || `${service.name} API  `);
      }

      return data.result;
    } catch (error) {
      console.error(`${service.name} API  :`, error);
      throw error;
    }
  }

  createPrompt(text) {
    return `     .

    . , , ,      .

**     :**
1.        .
2.  (: , , , )  .
3.       .
4. ,  , ,       .
5.  Mermaid, Graphviz, Markdown    /    , ,    .
6.      . ( ,   )
7.       .

---
     :
"""
${text}
"""

    .`;
  }
}

const aiServiceManager = new AIServiceManager();
export default aiServiceManager;
