import React, { useState, useEffect, useRef, useCallback } from "react";
import {
  Play,
  Pause,
  RotateCcw,
  Settings,
  Volume2,
  VolumeX,
  Clock,
} from "lucide-react";
import NotificationBanner from "./NotificationBanner";
import { useProductivity } from "../contexts/ProductivityContext";

const PomodoroTimer = ({ onTimerComplete }) => {
  const { addTimerLog } = useProductivity();
  const [timeLeft, setTimeLeft] = useState(25 * 60); // 25
  const [isRunning, setIsRunning] = useState(false);
  const [isBreak, setIsBreak] = useState(false);
  const [pomodoroCount, setPomodoroCount] = useState(0);
  const [breakCount, setBreakCount] = useState(0);
  const [isMuted, setIsMuted] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [notification, setNotification] = useState({
    show: false,
    message: "",
    type: "info",
  });
  const [settings, setSettings] = useState({
    workTime: 25,
    shortBreak: 5,
    longBreak: 15,
    longBreakInterval: 4,
    soundType: "default", // "default"  "f1"
  });

  const intervalRef = useRef(null);
  const audioRef = useRef(null);
  const f1AudioRef = useRef(null);

  //   (  )
  const playNotificationSound = useCallback(() => {
    if (isMuted) return;

    try {
      if (settings.soundType === "f1") {
        // F1    
        if (f1AudioRef.current) {
          f1AudioRef.current.currentTime = 0;
          f1AudioRef.current
            .play()
            .catch((e) => console.log("F1   :", e));
        }
      } else {
        //   (Web Audio API)
        const audioContext = new (window.AudioContext ||
          window.webkitAudioContext)();

        //   : - (2 )
        const playBeep = (frequency, startTime, duration) => {
          const oscillator = audioContext.createOscillator();
          const gainNode = audioContext.createGain();

          oscillator.connect(gainNode);
          gainNode.connect(audioContext.destination);

          oscillator.frequency.setValueAtTime(
            frequency,
            audioContext.currentTime
          );
          oscillator.type = "sine";

          gainNode.gain.setValueAtTime(0, audioContext.currentTime);
          gainNode.gain.linearRampToValueAtTime(
            0.3,
            audioContext.currentTime + 0.1
          );
          gainNode.gain.linearRampToValueAtTime(
            0.3,
            audioContext.currentTime + duration - 0.1
          );
          gainNode.gain.linearRampToValueAtTime(
            0,
            audioContext.currentTime + duration
          );

          oscillator.start(audioContext.currentTime + startTime);
          oscillator.stop(audioContext.currentTime + startTime + duration);
        };

        //   
        playBeep(800, 0, 0.3);

        //    (0.4 )
        playBeep(1000, 0.4, 0.3);
      }
    } catch (error) {
      console.log("  :", error);
    }
  }, [isMuted, settings.soundType]);

  //  
  const showNotification = useCallback((message, type = "info") => {
    setNotification({
      show: true,
      message,
      type,
    });
  }, []);

  //   
  const handleTimerComplete = useCallback(() => {
    if (!isBreak) {
      //   
      // ProductivityContext   
      addTimerLog(settings.workTime, "focus");
      if (onTimerComplete) {
        onTimerComplete(settings.workTime, "focus");
      }

      setPomodoroCount((prev) => prev + 1);
      const shouldTakeLongBreak =
        (pomodoroCount + 1) % settings.longBreakInterval === 0;
      const breakTime = shouldTakeLongBreak
        ? settings.longBreak
        : settings.shortBreak;
      setTimeLeft(breakTime * 60);
      setIsBreak(true);

      //  
      const message = shouldTakeLongBreak
        ? `  ! ${settings.longBreak}   .`
        : `  ! ${settings.shortBreak}  .`;
      showNotification(message, "success");
    } else {
      //   
      // ProductivityContext   
      const breakDuration =
        (pomodoroCount + 1) % settings.longBreakInterval === 0
          ? settings.longBreak
          : settings.shortBreak;
      addTimerLog(breakDuration, "break");
      if (onTimerComplete) {
        onTimerComplete(breakDuration, "break");
      }

      setBreakCount((prev) => prev + 1);
      setTimeLeft(settings.workTime * 60);
      setIsBreak(false);

      //  
      showNotification("   !   .", "warning");
    }
    setIsRunning(false);
  }, [
    isBreak,
    pomodoroCount,
    settings.longBreakInterval,
    settings.longBreak,
    settings.shortBreak,
    settings.workTime,
    showNotification,
    onTimerComplete,
    addTimerLog,
  ]);

  //  
  useEffect(() => {
    if (isRunning && timeLeft > 0) {
      intervalRef.current = setInterval(() => {
        setTimeLeft((prev) => {
          if (prev <= 1) {
            //  
            playNotificationSound();
            handleTimerComplete();
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    } else if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [isRunning, timeLeft, handleTimerComplete, playNotificationSound]);

  //  /
  const toggleTimer = () => {
    setIsRunning(!isRunning);
  };

  //  
  const resetTimer = () => {
    setIsRunning(false);
    setIsBreak(false);
    setTimeLeft(settings.workTime * 60);
  };

  //  
  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, "0")}:${secs
      .toString()
      .padStart(2, "0")}`;
  };

  //  
  const getProgress = () => {
    const totalTime = isBreak
      ? (pomodoroCount % settings.longBreakInterval === 0
          ? settings.longBreak
          : settings.shortBreak) * 60
      : settings.workTime * 60;
    return ((totalTime - timeLeft) / totalTime) * 100;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 p-4">
      <div className="max-w-4xl mx-auto pt-8">
        {/*  */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-800 mb-2 flex items-center justify-center gap-3">
            <Clock className="text-blue-600" size={40} />
             
          </h1>
          <p className="text-gray-600 text-lg">
                
          </p>
        </div>

        {/*   */}
        <div className="bg-white rounded-2xl shadow-xl p-8 mb-8">
          {/*   */}
          <div className="text-center mb-6">
            <div
              className={`inline-flex items-center px-4 py-2 rounded-full text-sm font-medium ${
                isBreak
                  ? "bg-green-100 text-green-800"
                  : "bg-blue-100 text-blue-800"
              }`}
            >
              {isBreak ? "  " : "  "}
            </div>
          </div>

          {/*   */}
          <div className="text-center mb-8">
            <div className="relative inline-block">
              {/*   */}
              <svg
                className="w-64 h-64 transform -rotate-90"
                viewBox="0 0 100 100"
              >
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  fill="none"
                  stroke="#e5e7eb"
                  strokeWidth="8"
                />
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  fill="none"
                  stroke={isBreak ? "#10b981" : "#3b82f6"}
                  strokeWidth="8"
                  strokeDasharray={`${2 * Math.PI * 45}`}
                  strokeDashoffset={`${
                    2 * Math.PI * 45 * (1 - getProgress() / 100)
                  }`}
                  strokeLinecap="round"
                  className="transition-all duration-1000 ease-out"
                />
              </svg>

              {/*   */}
              <div className="absolute inset-0 flex items-center justify-center">
                <div>
                  <div className="text-6xl font-bold text-gray-800 mb-2">
                    {formatTime(timeLeft)}
                  </div>
                  <div className="text-lg text-gray-600">
                    {isBreak ? " " : " "}
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/*   */}
          <div className="flex justify-center gap-4 mb-6">
            <button
              onClick={toggleTimer}
              className={`flex items-center gap-2 px-8 py-3 rounded-lg font-medium transition-all ${
                isRunning
                  ? "bg-red-600 hover:bg-red-700 text-white"
                  : "bg-blue-600 hover:bg-blue-700 text-white"
              }`}
            >
              {isRunning ? <Pause size={20} /> : <Play size={20} />}
              {isRunning ? "" : ""}
            </button>

            <button
              onClick={resetTimer}
              className="flex items-center gap-2 px-6 py-3 bg-gray-600 hover:bg-gray-700 text-white rounded-lg font-medium transition-all"
            >
              <RotateCcw size={20} />
              
            </button>

            <button
              onClick={() => setIsMuted(!isMuted)}
              className={`flex items-center gap-2 px-4 py-3 rounded-lg font-medium transition-all ${
                isMuted
                  ? "bg-gray-600 hover:bg-gray-700 text-white"
                  : "bg-green-600 hover:bg-green-700 text-white"
              }`}
            >
              {isMuted ? <VolumeX size={20} /> : <Volume2 size={20} />}
              {isMuted ? "" : ""}
            </button>

            <button
              onClick={playNotificationSound}
              className="flex items-center gap-2 px-4 py-3 bg-orange-600 hover:bg-orange-700 text-white rounded-lg font-medium transition-all"
            >
              {settings.soundType === "f1" ? " F1 " : " "}
            </button>

            <button
              onClick={() => setShowSettings(!showSettings)}
              className="flex items-center gap-2 px-4 py-3 bg-purple-600 hover:bg-purple-700 text-white rounded-lg font-medium transition-all"
            >
              <Settings size={20} />
              
            </button>
          </div>

          {/*  */}
          <div className="grid grid-cols-2 gap-4 text-center">
            <div className="bg-blue-50 rounded-lg p-4">
              <div className="text-2xl font-bold text-blue-600">
                {pomodoroCount}
              </div>
              <div className="text-sm text-gray-600"> </div>
            </div>
            <div className="bg-green-50 rounded-lg p-4">
              <div className="text-2xl font-bold text-green-600">
                {breakCount}
              </div>
              <div className="text-sm text-gray-600"> </div>
            </div>
          </div>
        </div>

        {/*   */}
        {showSettings && (
          <div className="bg-white rounded-2xl shadow-xl p-8 mb-8">
            <h3 className="text-xl font-semibold text-gray-800 mb-6">
               
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                    ()
                </label>
                <input
                  type="number"
                  min="1"
                  max="60"
                  value={settings.workTime}
                  onChange={(e) =>
                    setSettings((prev) => ({
                      ...prev,
                      workTime: parseInt(e.target.value),
                    }))
                  }
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                    ()
                </label>
                <input
                  type="number"
                  min="1"
                  max="30"
                  value={settings.shortBreak}
                  onChange={(e) =>
                    setSettings((prev) => ({
                      ...prev,
                      shortBreak: parseInt(e.target.value),
                    }))
                  }
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                    ()
                </label>
                <input
                  type="number"
                  min="1"
                  max="60"
                  value={settings.longBreak}
                  onChange={(e) =>
                    setSettings((prev) => ({
                      ...prev,
                      longBreak: parseInt(e.target.value),
                    }))
                  }
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                     ( )
                </label>
                <input
                  type="number"
                  min="1"
                  max="10"
                  value={settings.longBreakInterval}
                  onChange={(e) =>
                    setSettings((prev) => ({
                      ...prev,
                      longBreakInterval: parseInt(e.target.value),
                    }))
                  }
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <select
                  value={settings.soundType}
                  onChange={(e) =>
                    setSettings((prev) => ({
                      ...prev,
                      soundType: e.target.value,
                    }))
                  }
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="default">  </option>
                  <option value="f1"> F1 </option>
                </select>
              </div>
            </div>
            <div className="mt-6 text-center">
              <button
                onClick={() => {
                  setShowSettings(false);
                  resetTimer();
                }}
                className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium transition-all"
              >
                 
              </button>
            </div>
          </div>
        )}

        {/*    */}
        <div className="bg-white rounded-2xl shadow-xl p-8">
          <h3 className="text-xl font-semibold text-gray-800 mb-4">
              ?
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-sm text-gray-600">
            <div>
              <h4 className="font-medium text-gray-800 mb-2">
                   (25)
              </h4>
              <p>
                     .   
                   .
              </p>
            </div>
            <div>
              <h4 className="font-medium text-gray-800 mb-2">
                   (5)
              </h4>
              <p>
                    .  
                 .
              </p>
            </div>
            <div>
              <h4 className="font-medium text-gray-800 mb-2">
                   (15)
              </h4>
              <p>
                4       .   
                .
              </p>
            </div>
            <div>
              <h4 className="font-medium text-gray-800 mb-2"> </h4>
              <p>
                 ,  ,   ,   
                .
              </p>
            </div>
          </div>
        </div>

        {/*   */}
        <audio ref={audioRef} preload="auto" style={{ display: "none" }}>
          <source
            src="data:audio/wav;base64,UklGRnoGAABXQVZFZm10IBAAAAABAAEAQB8AAEAfAAABAAgAZGF0YQoGAACBhYqFbF1fdJivrJBhNjVgodDbq2EcBj+a2/LDciUFLIHO8tiJNwgZaLvt559NEAxQp+PwtmMcBjiR1/LMeSwFJHfH8N2QQAoUXrTp66hVFApGn+DyvmwhBSuBzvLZiTYIG2m98OScTgwOUarm7blmGgU7k9n1unEiBC13yO/eizEIHWq+8+OWT"
            type="audio/wav"
          />
        </audio>
        <audio ref={f1AudioRef} preload="auto" style={{ display: "none" }}>
          <source src="/TeamRadioF1FX.wav" type="audio/wav" />
        </audio>

        {/*   */}
        <NotificationBanner
          show={notification.show}
          message={notification.message}
          type={notification.type}
          onClose={() =>
            setNotification({ show: false, message: "", type: "info" })
          }
        />
      </div>
    </div>
  );
};

export default PomodoroTimer;
