import React, { useState, useEffect, useCallback } from "react";
import { CheckCircle, XCircle, Clock, AlertTriangle } from "lucide-react";

// API  URL  -  IP 
const getApiBaseUrl = () => {
  if (process.env.REACT_APP_API_URL) {
    return process.env.REACT_APP_API_URL;
  }
  const hostname = window.location.hostname;
  return `http://${hostname}:3001`;
};

const API_BASE_URL = getApiBaseUrl();

//      
const ServerDataItem = ({ itemKey, itemName }) => {
  const [hasData, setHasData] = useState(false);

  //   
  useEffect(() => {
    const checkServerData = async () => {
      try {
        const response = await fetch(`${API_BASE_URL}/api/${itemKey}`);
        if (response.ok) {
          const data = await response.json();
          setHasData(data.logs && data.logs.length > 0);
        }
      } catch (error) {
        console.error(`   : ${itemKey}`, error);
      }
    };
    checkServerData();
  }, [itemKey]);

  const getStatusIcon = (success) => {
    return success ? (
      <CheckCircle className="text-green-500" size={16} />
    ) : (
      <XCircle className="text-red-500" size={16} />
    );
  };

  return (
    <div className="flex items-center justify-between">
      <span className="text-sm text-gray-600">{itemName}</span>
      <div className="flex items-center gap-2">
        {getStatusIcon(hasData)}
        <span className="text-sm font-medium">
          {hasData ? " " : " "}
        </span>
      </div>
    </div>
  );
};

const APITest = () => {
  const [testResults, setTestResults] = useState({});
  const [isTesting, setIsTesting] = useState(false);

  const testBackendAPI = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/api/status`);
      const data = await response.json();
      return { success: true, data };
    } catch (error) {
      return { success: false, error: error.message };
    }
  };

  const testAIAPI = async (serviceName) => {
    try {
      //    AI API 
      const response = await fetch(
        `${API_BASE_URL}/api/ai/${serviceName.toLowerCase()}`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            prompt: "Hello, this is a test message.",
            apiKey: "test-key", //    API  
          }),
        }
      );

      if (response.status === 400) {
        // API      (  )
        return {
          success: true,
          status: response.status,
          message: "  (API  )",
        };
      }

      return { success: response.ok, status: response.status };
    } catch (error) {
      return { success: false, error: error.message };
    }
  };

  const runAllTests = useCallback(async () => {
    setIsTesting(true);
    const results = {};

    //  API 
    console.log(" API  ...");
    results.backend = await testBackendAPI();

    // AI API  (  )
    console.log("AI API  ...");
    results.claude = await testAIAPI("claude");
    results.gpt = await testAIAPI("gpt");
    results.groq = await testAIAPI("groq");
    results.perplexity = await testAIAPI("perplexity");
    results.gemini = await testAIAPI("gemini");

    setTestResults(results);
    setIsTesting(false);
  }, []);

  useEffect(() => {
    runAllTests();
  }, [runAllTests]); // runAllTests  

  const getStatusIcon = (success) => {
    if (success === undefined)
      return <Clock className="text-gray-400" size={16} />;
    return success ? (
      <CheckCircle className="text-green-500" size={16} />
    ) : (
      <XCircle className="text-red-500" size={16} />
    );
  };

  const getStatusText = (success, message) => {
    if (success === undefined) return " ...";
    if (message) return message;
    return success ? "" : " ";
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <div className="mb-6">
        <h1 className="text-3xl font-bold text-gray-800 mb-2">
          API   
        </h1>
        <p className="text-gray-600"> API   </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/*  API */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
              API
          </h3>
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600"> </span>
              <div className="flex items-center gap-2">
                {getStatusIcon(testResults.backend?.success)}
                <span className="text-sm font-medium">
                  {getStatusText(testResults.backend?.success)}
                </span>
              </div>
            </div>
            {testResults.backend?.success && (
              <div className="text-xs text-gray-500 bg-gray-50 p-2 rounded">
                <p>
                   :{" "}
                  {testResults.backend.data?.isBlockingEnabled
                    ? ""
                    : ""}
                </p>
                <p>
                    :{" "}
                  {testResults.backend.data?.blockStats?.totalBlocks || 0}
                </p>
              </div>
            )}
            {testResults.backend?.error && (
              <div className="text-xs text-red-500 bg-red-50 p-2 rounded">
                : {testResults.backend.error}
              </div>
            )}
          </div>
        </div>

        {/* AI API */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
            AI  API
          </h3>
          <div className="space-y-3">
            {["claude", "gpt", "groq", "perplexity", "gemini"].map(
              (service) => (
                <div
                  key={service}
                  className="flex items-center justify-between"
                >
                  <span className="text-sm text-gray-600 capitalize">
                    {service}
                  </span>
                  <div className="flex items-center gap-2">
                    {getStatusIcon(testResults[service]?.success)}
                    <span className="text-sm font-medium">
                      {getStatusText(
                        testResults[service]?.success,
                        testResults[service]?.message
                      )}
                    </span>
                  </div>
                </div>
              )
            )}
          </div>
        </div>

        {/*   */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
             
          </h3>
          <div className="space-y-3">
            {[
              {
                key: "productivity_timer_logs",
                name: "  (localStorage)",
              },
              {
                key: "productivity_block_logs",
                name: "  (localStorage)",
              },
              { key: "productivity_work_sessions", name: " " },
              { key: "productivity_data", name: " " },
            ].map((item) => {
              const data = localStorage.getItem(item.key);
              const hasData = data && JSON.parse(data).length > 0;
              return (
                <div
                  key={item.key}
                  className="flex items-center justify-between"
                >
                  <span className="text-sm text-gray-600">{item.name}</span>
                  <div className="flex items-center gap-2">
                    {getStatusIcon(hasData)}
                    <span className="text-sm font-medium">
                      {hasData ? " " : " "}
                    </span>
                  </div>
                </div>
              );
            })}
          </div>
        </div>

        {/*    */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
              
          </h3>
          <div className="space-y-3">
            {[
              { key: "timer-logs", name: "  ()" },
              { key: "block-logs", name: "  ()" },
            ].map((item) => (
              <ServerDataItem
                key={item.key}
                itemKey={item.key}
                itemName={item.name}
              />
            ))}
          </div>
        </div>

        {/* API   */}
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h3 className="text-lg font-semibold text-gray-800 mb-4">
            API  
          </h3>
          <div className="space-y-3">
            {["claude", "gpt", "groq", "perplexity", "gemini"].map(
              (service) => {
                const apiKey = localStorage.getItem(`${service}_api_key`);
                const hasKey = apiKey && apiKey.length > 0;
                return (
                  <div
                    key={service}
                    className="flex items-center justify-between"
                  >
                    <span className="text-sm text-gray-600 capitalize">
                      {service}
                    </span>
                    <div className="flex items-center gap-2">
                      {getStatusIcon(hasKey)}
                      <span className="text-sm font-medium">
                        {hasKey ? "" : ""}
                      </span>
                    </div>
                  </div>
                );
              }
            )}
          </div>
        </div>
      </div>

      <div className="mt-6 flex justify-center">
        <button
          onClick={runAllTests}
          disabled={isTesting}
          className="flex items-center gap-2 px-6 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50"
        >
          {isTesting ? (
            <>
              <Clock className="animate-spin" size={16} />
               ...
            </>
          ) : (
            <>
              <AlertTriangle size={16} />
               
            </>
          )}
        </button>
      </div>

      <div className="mt-6 bg-yellow-50 border border-yellow-200 rounded-lg p-4">
        <h4 className="font-semibold text-yellow-800 mb-2">  </h4>
        <ul className="text-sm text-yellow-700 space-y-1">
          <li>
             <strong> </strong>:     
          </li>
          <li>
             <strong>AI API</strong>:     AI  
          </li>
          <li>
             <strong> </strong>:     
          </li>
          <li>
             <strong>API </strong>: AI     
          </li>
        </ul>
      </div>
    </div>
  );
};

export default APITest;
