import React, { useState, useEffect, useCallback, useMemo } from "react";
import {
  Shield,
  Lock,
  AlertTriangle,
  CheckCircle,
  XCircle,
  Clock,
  Target,
} from "lucide-react";
import NotificationBanner from "./NotificationBanner";
import AlgorithmProblemModal from "./AlgorithmProblemModal";
import { useProductivity } from "../contexts/ProductivityContext";

// API  URL 
const getApiBaseUrl = () => {
  if (process.env.REACT_APP_API_URL) {
    return process.env.REACT_APP_API_URL;
  }
  const hostname = window.location.hostname;
  //   NGROK URL 
  if (hostname === "eieconcierge.com" || hostname === "www.eieconcierge.com") {
    return "https://be8b2c8c5bb3.ngrok-free.app"; // NGROK URL
  }
  return `http://${hostname}:3001`;
};

const API_BASE_URL = getApiBaseUrl();

const BlockPage = ({ onBlockComplete }) => {
  const { addBlockLog } = useProductivity();
  const [blockedSites, setBlockedSites] = useState([]);
  const [blockStats, setBlockStats] = useState({
    totalBlocks: 0,
    todayBlocks: 0,
    weeklyBlocks: 0,
    monthlyBlocks: 0,
  });
  const [isBlockingEnabled, setIsBlockingEnabled] = useState(false);
  const [isScheduleActive, setIsScheduleActive] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [notification, setNotification] = useState({
    show: false,
    message: "",
    type: "info",
  });
  const [blockSchedule, setBlockSchedule] = useState({
    startTime: "09:00",
    endTime: "18:00",
    days: ["", "", "", "", ""],
  });

  //    
  const [algorithmProblem, setAlgorithmProblem] = useState(null);
  const [isAlgorithmModalOpen, setIsAlgorithmModalOpen] = useState(false);
  const [testResults, setTestResults] = useState(null);
  const [isSubmittingCode, setIsSubmittingCode] = useState(false);
  const [pendingAction, setPendingAction] = useState(null); // 'stop'  'schedule'

  //   
  const defaultBlockedSites = useMemo(
    () => [
      {
        name: "YouTube",
        domain: "youtube.com",
        category: "video",
        isBlocked: true,
      },
      {
        name: "Facebook",
        domain: "facebook.com",
        category: "social",
        isBlocked: true,
      },
      {
        name: "Instagram",
        domain: "instagram.com",
        category: "social",
        isBlocked: true,
      },
      {
        name: "Twitter",
        domain: "twitter.com",
        category: "social",
        isBlocked: true,
      },
      {
        name: "TikTok",
        domain: "tiktok.com",
        category: "video",
        isBlocked: true,
      },
      {
        name: "Reddit",
        domain: "reddit.com",
        category: "social",
        isBlocked: true,
      },
      {
        name: "Netflix",
        domain: "netflix.com",
        category: "video",
        isBlocked: false,
      },
      {
        name: "Twitch",
        domain: "twitch.tv",
        category: "video",
        isBlocked: false,
      },
      {
        name: "Discord",
        domain: "discord.com",
        category: "social",
        isBlocked: false,
      },
      {
        name: "Telegram",
        domain: "telegram.org",
        category: "social",
        isBlocked: false,
      },
    ],
    []
  );

  //    
  const fetchBlockStatus = useCallback(async () => {
    try {
      // API_BASE_URL   
      if (!API_BASE_URL) {
        console.log("   ");
        return;
      }

      const response = await fetch(`${API_BASE_URL}/api/block/status`);
      const data = await response.json();

      if (data.success) {
        setIsBlockingEnabled(data.isBlockingEnabled);
        setIsScheduleActive(data.isScheduleActive || false);
        setBlockStats(
          data.blockStats || {
            totalBlocks: 0,
            todayBlocks: 0,
            weeklyBlocks: 0,
            monthlyBlocks: 0,
          }
        );

        //    
        if (data.userSettings?.blockedSites) {
          setBlockedSites((prev) =>
            prev.map((site) => ({
              ...site,
              isBlocked: data.userSettings.blockedSites.includes(site.domain),
            }))
          );
        }

        if (data.userSettings?.blockSchedule) {
          setBlockSchedule(data.userSettings.blockSchedule);
        }
      }
    } catch (error) {
      console.error("   :", error);
      console.log("  ");
    }
  }, []); //      

  useEffect(() => {
    setBlockedSites(defaultBlockedSites);
    //       
    fetchBlockStatus();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [defaultBlockedSites]); // fetchBlockStatus    

  //    
  const handleAlgorithmSubmit = async (code) => {
    setIsSubmittingCode(true);
    setTestResults(null);

    try {
      let endpoint = "";
      let requestBody = {
        code,
        problemId: algorithmProblem.id,
      };

      if (pendingAction === "stop") {
        endpoint = `${API_BASE_URL}/api/block/stop`;
      } else if (pendingAction === "schedule") {
        endpoint = `${API_BASE_URL}/api/settings/block-schedule`;
        requestBody = {
          ...requestBody,
          startTime: blockSchedule.startTime,
          endTime: blockSchedule.endTime,
          days: blockSchedule.days,
        };
      }

      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestBody),
      });

      const data = await response.json();

      if (data.success) {
        //    
        setIsAlgorithmModalOpen(false);
        setAlgorithmProblem(null);
        setPendingAction(null);

        setNotification({
          show: true,
          message: data.message || "    !",
          type: "success",
        });

        //  
        if (pendingAction === "stop") {
          setIsBlockingEnabled(false);
        }

        //     
        fetchBlockStatus();
      } else {
        //     
        if (data.testResults) {
          setTestResults(data.testResults);
        }

        setNotification({
          show: true,
          message: data.error || "   ",
          type: "error",
        });
      }
    } catch (error) {
      setNotification({
        show: true,
        message: "   : " + error.message,
        type: "error",
      });
    } finally {
      setIsSubmittingCode(false);
    }
  };

  //   API 
  const callBlockAPI = async (action) => {
    setIsLoading(true);
    try {
      const response = await fetch(`${API_BASE_URL}/api/block/${action}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      });

      const data = await response.json();

      if (data.success) {
        if (action === "start") {
          setIsBlockingEnabled(true);
          setBlockStats(data.blockStats);
        } else {
          setIsBlockingEnabled(false);
        }
      }

      return data;
    } catch (error) {
      console.error(" API  :", error);
      return { success: false, error: error.message };
    } finally {
      setIsLoading(false);
    }
  };

  //  
  const startBlocking = async () => {
    const result = await callBlockAPI("start");
    if (result.success) {
      //   
      addBlockLog(0, "  ");
      if (onBlockComplete) {
        //    
        localStorage.setItem("block_start_time", new Date().toISOString());
        onBlockComplete(0, "  ");
      }

      setNotification({
        show: true,
        message: "    !",
        type: "success",
      });
    } else {
      setNotification({
        show: true,
        message: "   : " + result.error,
        type: "error",
      });
    }
  };

  //  
  const stopBlocking = async () => {
    if (!window.confirm("    ?")) return;

    const result = await callBlockAPI("stop");
    if (result.success) {
      //    (   )
      //        
      const blockStartTime = localStorage.getItem("block_start_time");
      let blockDuration = 30; // 

      if (blockStartTime) {
        const startTime = new Date(blockStartTime);
        const endTime = new Date();
        blockDuration = Math.round((endTime - startTime) / (1000 * 60)); //  
        localStorage.removeItem("block_start_time");
      }

      addBlockLog(blockDuration, "  ");
      if (onBlockComplete) {
        onBlockComplete(blockDuration, "  ");
      }

      setNotification({
        show: true,
        message: "    !",
        type: "success",
      });
    } else {
      //     
      if (result.requiresProblem && result.problem) {
        setAlgorithmProblem(result.problem);
        setPendingAction("stop");
        setIsAlgorithmModalOpen(true);
        setTestResults(null);
        return;
      }

      //       
      if (result.scheduleInfo?.isScheduleActive) {
        const { currentDay, endTime } = result.scheduleInfo;
        setNotification({
          show: true,
          message: `    . ${currentDay} ${endTime}  .`,
          type: "warning",
        });
      } else {
        setNotification({
          show: true,
          message: "   : " + result.error,
          type: "error",
        });
      }
    }
  };

  //  / 
  const toggleSiteBlock = async (domain) => {
    const updatedSites = blockedSites.map((site) =>
      site.domain === domain ? { ...site, isBlocked: !site.isBlocked } : site
    );
    setBlockedSites(updatedSites);

    //   
    try {
      const blockedDomains = updatedSites
        .filter((site) => site.isBlocked)
        .map((site) => site.domain);

      await fetch(`${API_BASE_URL}/api/settings/blocked-sites`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ blockedSites: blockedDomains }),
      });

      //   
      setNotification({
        show: true,
        message: "    .",
        type: "success",
      });
    } catch (error) {
      console.error("    :", error);
      setNotification({
        show: true,
        message: "   : " + error.message,
        type: "error",
      });
    }
  };

  //  /
  const toggleAllSites = async (block) => {
    const updatedSites = blockedSites.map((site) => ({
      ...site,
      isBlocked: block,
    }));
    setBlockedSites(updatedSites);

    //   
    try {
      const blockedDomains = updatedSites
        .filter((site) => site.isBlocked)
        .map((site) => site.domain);

      await fetch(`${API_BASE_URL}/api/settings/blocked-sites`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ blockedSites: blockedDomains }),
      });

      //   
      setNotification({
        show: true,
        message: "    .",
        type: "success",
      });
    } catch (error) {
      console.error("    :", error);
      setNotification({
        show: true,
        message: "   : " + error.message,
        type: "error",
      });
    }
  };

  //  
  const getSitesByCategory = (category) => {
    return blockedSites?.filter((site) => site.category === category) || [];
  };

  //    
  const saveBlockSchedule = useCallback(async (newSchedule) => {
    try {
      const response = await fetch(
        `${API_BASE_URL}/api/settings/block-schedule`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            startTime: newSchedule.startTime,
            endTime: newSchedule.endTime,
            days: newSchedule.days,
          }),
        }
      );

      const data = await response.json();

      if (data.success) {
        //   
        setNotification({
          show: true,
          message: data.message || "    .",
          type: "success",
        });
      } else {
        //     
        if (data.requiresProblem && data.problem) {
          setAlgorithmProblem(data.problem);
          setPendingAction("schedule");
          setIsAlgorithmModalOpen(true);
          setTestResults(null);
          return;
        }

        setNotification({
          show: true,
          message: "   : " + data.error,
          type: "error",
        });
      }
    } catch (error) {
      console.error("    :", error);
      setNotification({
        show: true,
        message: "    : " + error.message,
        type: "error",
      });
    }
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-red-50 to-orange-100 p-4">
      <div className="max-w-7xl mx-auto pt-8">
        {/*  */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-800 mb-2 flex items-center justify-center gap-3">
            <Shield className="text-red-600" size={40} />
               
          </h1>
          <p className="text-gray-600 text-lg">
                
          </p>
        </div>

        {/*   */}
        <div className="bg-white rounded-2xl shadow-xl p-6 mb-8">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-3">
              <div
                className={`w-4 h-4 rounded-full ${
                  isBlockingEnabled ? "bg-green-500" : "bg-gray-300"
                }`}
              ></div>
              <span className="text-lg font-semibold text-gray-800">
                {isBlockingEnabled
                  ? isScheduleActive
                    ? "  "
                    : "  "
                  : "  "}
              </span>
              {isScheduleActive && (
                <span className="text-sm text-orange-600 bg-orange-100 px-2 py-1 rounded-full">
                   
                </span>
              )}
            </div>
            <div className="flex gap-2">
              <button
                onClick={startBlocking}
                disabled={isLoading || isBlockingEnabled}
                className={`px-6 py-2 rounded-lg font-medium transition-all ${
                  isLoading || isBlockingEnabled
                    ? "bg-gray-400 cursor-not-allowed"
                    : "bg-green-600 hover:bg-green-700 text-white"
                }`}
              >
                {isLoading ? " ..." : " "}
              </button>
              <button
                onClick={stopBlocking}
                disabled={isLoading || !isBlockingEnabled || isScheduleActive}
                className={`px-6 py-2 rounded-lg font-medium transition-all ${
                  isLoading || !isBlockingEnabled || isScheduleActive
                    ? "bg-gray-400 cursor-not-allowed"
                    : "bg-red-600 hover:bg-red-700 text-white"
                }`}
              >
                {isLoading
                  ? " ..."
                  : isScheduleActive
                  ? "  "
                  : " "}
              </button>
            </div>
          </div>

          {/*  */}
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="bg-blue-50 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-blue-600">
                {blockStats.totalBlocks}
              </div>
              <div className="text-sm text-gray-600">  </div>
            </div>
            <div className="bg-green-50 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-green-600">
                {blockStats.todayBlocks}
              </div>
              <div className="text-sm text-gray-600">  </div>
            </div>
            <div className="bg-yellow-50 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-yellow-600">
                {blockStats.weeklyBlocks}
              </div>
              <div className="text-sm text-gray-600">  </div>
            </div>
            <div className="bg-purple-50 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-purple-600">
                {blockStats.monthlyBlocks}
              </div>
              <div className="text-sm text-gray-600">  </div>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/*    */}
          <div className="bg-white rounded-2xl shadow-xl p-6">
            <div className="flex items-center justify-between mb-6">
              <h3 className="text-xl font-semibold text-gray-800 flex items-center gap-2">
                <Lock size={24} />
                  
              </h3>
              <div className="flex gap-2">
                <button
                  onClick={() => toggleAllSites(true)}
                  className="px-3 py-1 bg-red-600 hover:bg-red-700 text-white text-sm rounded-lg transition-all"
                >
                   
                </button>
                <button
                  onClick={() => toggleAllSites(false)}
                  className="px-3 py-1 bg-green-600 hover:bg-green-700 text-white text-sm rounded-lg transition-all"
                >
                   
                </button>
              </div>
            </div>

            {/*   */}
            <div className="mb-6">
              <h4 className="font-medium text-gray-800 mb-3 flex items-center gap-2">
                <Target size={16} />
                 
              </h4>
              <div className="space-y-2">
                {getSitesByCategory("video").map((site) => (
                  <div
                    key={site.domain}
                    className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                  >
                    <div className="flex items-center gap-3">
                      <div
                        className={`w-3 h-3 rounded-full ${
                          site.isBlocked ? "bg-red-500" : "bg-green-500"
                        }`}
                      ></div>
                      <span className="font-medium">{site.name}</span>
                      <span className="text-sm text-gray-500">
                        ({site.domain})
                      </span>
                    </div>
                    <button
                      onClick={() => toggleSiteBlock(site.domain)}
                      className={`px-3 py-1 rounded text-sm font-medium transition-all ${
                        site.isBlocked
                          ? "bg-green-100 text-green-700 hover:bg-green-200"
                          : "bg-red-100 text-red-700 hover:bg-red-200"
                      }`}
                    >
                      {site.isBlocked ? "" : ""}
                    </button>
                  </div>
                ))}
              </div>
            </div>

            {/*   */}
            <div>
              <h4 className="font-medium text-gray-800 mb-3 flex items-center gap-2">
                <Target size={16} />
                 
              </h4>
              <div className="space-y-2">
                {getSitesByCategory("social").map((site) => (
                  <div
                    key={site.domain}
                    className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                  >
                    <div className="flex items-center gap-3">
                      <div
                        className={`w-3 h-3 rounded-full ${
                          site.isBlocked ? "bg-red-500" : "bg-green-500"
                        }`}
                      ></div>
                      <span className="font-medium">{site.name}</span>
                      <span className="text-sm text-gray-500">
                        ({site.domain})
                      </span>
                    </div>
                    <button
                      onClick={() => toggleSiteBlock(site.domain)}
                      className={`px-3 py-1 rounded text-sm font-medium transition-all ${
                        site.isBlocked
                          ? "bg-green-100 text-green-700 hover:bg-green-200"
                          : "bg-red-100 text-red-700 hover:bg-red-200"
                      }`}
                    >
                      {site.isBlocked ? "" : ""}
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/*   */}
          <div className="bg-white rounded-2xl shadow-xl p-6">
            <h3 className="text-xl font-semibold text-gray-800 mb-6 flex items-center gap-2">
              <Clock size={24} />
               
            </h3>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <input
                  type="time"
                  value={blockSchedule.startTime}
                  onChange={(e) => {
                    const newSchedule = {
                      ...blockSchedule,
                      startTime: e.target.value,
                    };
                    setBlockSchedule(newSchedule);
                    saveBlockSchedule(newSchedule);
                  }}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <input
                  type="time"
                  value={blockSchedule.endTime}
                  onChange={(e) => {
                    const newSchedule = {
                      ...blockSchedule,
                      endTime: e.target.value,
                    };
                    setBlockSchedule(newSchedule);
                    saveBlockSchedule(newSchedule);
                  }}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   
                </label>
                <div className="flex flex-wrap gap-2">
                  {["", "", "", "", "", "", ""].map((day) => (
                    <button
                      key={day}
                      onClick={() => {
                        const newDays = blockSchedule.days.includes(day)
                          ? blockSchedule.days.filter((d) => d !== day)
                          : [...blockSchedule.days, day];
                        const newSchedule = {
                          ...blockSchedule,
                          days: newDays,
                        };
                        setBlockSchedule(newSchedule);
                        saveBlockSchedule(newSchedule);
                      }}
                      className={`px-3 py-1 rounded-lg text-sm font-medium transition-all ${
                        blockSchedule.days.includes(day)
                          ? "bg-red-600 text-white"
                          : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                      }`}
                    >
                      {day}
                    </button>
                  ))}
                </div>
              </div>

              <div className="pt-4">
                <button className="w-full px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded-lg font-medium transition-all">
                   
                </button>
              </div>
            </div>
          </div>
        </div>

        {/*     */}
        <div className="bg-white rounded-2xl shadow-xl p-8">
          <h3 className="text-xl font-semibold text-gray-800 mb-6 flex items-center gap-2">
            <AlertTriangle size={24} />
                
          </h3>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h4 className="font-medium text-gray-800 mb-3 flex items-center gap-2">
                <CheckCircle size={16} className="text-green-600" />
                 
              </h4>
              <ul className="space-y-2 text-sm text-gray-600">
                <li>      </li>
                <li>    </li>
                <li>      </li>
                <li>   </li>
                <li>   </li>
              </ul>
            </div>

            <div>
              <h4 className="font-medium text-gray-800 mb-3 flex items-center gap-2">
                <XCircle size={16} className="text-red-600" />
                
              </h4>
              <ul className="space-y-2 text-sm text-gray-600">
                <li>      </li>
                <li>      </li>
                <li>      </li>
                <li> VPN      </li>
                <li>     </li>
              </ul>
            </div>
          </div>

          <div className="mt-6 p-4 bg-blue-50 rounded-lg">
            <h4 className="font-medium text-blue-800 mb-2">
                
            </h4>
            <p className="text-sm text-blue-700">
                   .   
               ,       
              .
            </p>
          </div>
        </div>

        {/*   */}
        <NotificationBanner
          show={notification.show}
          message={notification.message}
          type={notification.type}
          onClose={() =>
            setNotification({ show: false, message: "", type: "info" })
          }
        />

        {/*     */}
        <AlgorithmProblemModal
          isOpen={isAlgorithmModalOpen}
          onClose={() => {
            setIsAlgorithmModalOpen(false);
            setAlgorithmProblem(null);
            setPendingAction(null);
            setTestResults(null);
          }}
          problem={algorithmProblem}
          onSubmit={handleAlgorithmSubmit}
          testResults={testResults}
          isSubmitting={isSubmittingCode}
        />
      </div>
    </div>
  );
};

export default BlockPage;
